/** @format */
@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+Arabic&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "IBM Plex Sans Arabic", sans-serif;
}
body {
  height: 100vh;
}

.rs {
  color: #3081a1;
}
.c_height {
  height: 600px;
}
.textbox {
  position: absolute;
  top: 185px;
  left: 50px;
}
.underline {
  width: 700px;
  margin: auto;
}
.services {
  margin-right: 10px;
}
.box_container {
  height: 500px;
  width: 100%;
  padding: 50px 10px;
}
.contact_container {
  height: 500px;
  width: 100%;
}
.card_box {
  margin: 10px 10px;
}
.footer {
  height: 45px;
  color: #000;
}
@media (min-width: 576px) {
  .home {
    height: 499px;
  }
  .imgmedia {
    width: auto;
    height: 499px;
  }
  body {
    height: fit-content;
  }
}

@media (min-width: 768px) {
  .underline {
    width: 500px;
  }

  .imgmedia {
    width: fit-content;
    height: 499px;
  }
  .footer {
    display: none;
  }
}

@media (min-width: 992px) {
  .imgmedia {
    width: fit-content;
    height: 545px;
  }
  .footer {
    display: none;
  }
}

@media (min-width: 1200px) {
  .imgmedia {
    width: fit-content;
    height: 550px;
  }
}
